import React, { useEffect, useState, useContext } from 'react';
import ReaperVaults from './reaper';
import axios from 'axios';
import GrimVaults from './grim';

const FANTOM_CHAIN_ID = 250;

const BEEFY_URLS = {
  137: 'https://raw.githubusercontent.com/beefyfinance/beefy-app/prod/src/features/configure/vault/polygon_pools.js',
  250: 'https://raw.githubusercontent.com/beefyfinance/beefy-app/prod/src/features/configure/vault/fantom_pools.js',
  56: 'https://raw.githubusercontent.com/beefyfinance/beefy-app/prod/src/features/configure/vault/bsc_pools.js',
};

export const useBeefyVaults = (initChainId) => {
  const [vaults, setVaults] = useState([]);
  const [chainId, setChainId] = useState(initChainId);

  const getVaults = async (endPoint) => {
    try {
      console.log("Getting endpoint: ", endPoint);
      const response = await axios.get(endPoint);
      const data = response.data;
      let vaults = '[' + data.substring(data.indexOf('\n') + 1);
      const allVaults = eval(vaults);
      const selectedVaults = allVaults.filter(v => {
        return !v.depositsPaused && v.status === 'active' &&
          ((v.name?.includes('LP')) ||
            ((v.name?.match(/\//g) || []).length < 2 &&
              !v.name?.includes('LP')));
      });
      const formattedVaults = selectedVaults
        .map((v) => {
          return {
            name: `Beefy ${v.name} Vault`,
            token: v.tokenAddress,
            isLP: v.name?.includes('LP'),
            provider: 'BEEFY',
            address: v.earnContractAddress,
          };
        })
        .sort((a, b) => {
          if (a.name > b.name) {
            return 1;
          }
          if (a.name < b.name) {
            return -1;
          }
          return 0;
        });

      console.log("Got vaults: ", formattedVaults);

      setVaults(formattedVaults);
      console.log("Vaults were set");
    } catch (err) {
      console.error(err);
    }
  };

  const getGrimVaults = () => {
    console.log(`Getting grim vaults`);
    let vaults = [];

    for (const provider of GrimVaults) {
      console.log(provider['NAME']);
      console.log(provider);

      for (const vault of provider["vaults"]) {
        vaults.push({
          name: `Grim ${vault["lpname"]} Vault`,
          token: vault["lpaddress"],
          isLP: vault["lpname"].includes("-"),
          provider: 'GRIM',
          address: vault["vault"],
          router: provider["ROUTER"],
        });
      }
    }

    return vaults.sort((a, b) => {
      if (a.name > b.name) {
        return 1;
      }
      if (a.name < b.name) {
        return -1;
      }
      return 0;
    });
  };

  const getReaperVaults = () => {
    console.log(`Getting reaper vaults`);
    let vaults = ReaperVaults["spirit"]["farms"];
    let router = ReaperVaults["spirit"]["uniRouter"];
    const spiritVaults = vaults.filter((v) => !v["deprecated"]).map((v) => {
      return {
        name: `Reaper ${v["name"]}`,
        token: v["lpToken"]["address"],
        isLP: v["addLiquidity"] !== undefined,
        provider: 'REAPER',
        address: v["vault"]["address"],
        router: router,
      };
    });

    vaults = ReaperVaults["spooky"]["farms"];
    router = ReaperVaults["spooky"]["uniRouter"];

    const spookyVaults = vaults.filter((v) => !v["deprecated"]).map((v) => {
      return {
        name: `Reaper ${v["name"]}`,
        token: v["lpToken"]["address"],
        isLP: v["addLiquidity"] !== undefined,
        provider: 'REAPER',
        address: v["vault"]["address"],
        router: router,
      };
    });

    const formattedVaults = spiritVaults.concat(spookyVaults).sort((a, b) => {
      if (a.name > b.name) {
        return 1;
      }
      if (a.name < b.name) {
        return -1;
      }
      return 0;
    });

    return formattedVaults;
  };

  useEffect(() => {
    if (chainId === FANTOM_CHAIN_ID) {
      const reaperVaults = getReaperVaults();
      const grimVaults = getGrimVaults();
      const allVaults = [...grimVaults, ...reaperVaults];

      setVaults(allVaults);
    } else {
      getVaults(BEEFY_URLS[chainId]);
    }
  }, [chainId]);

  return [vaults, setChainId];
};
